import React, { Component } from 'react'
import { Switch, Route } from "react-router-dom";
import MovieSearch from '../components/MovieSearch';
import MovieDetail from '../components/MovieDetail';
import FavouriteMovies from '../components/FavouriteMovies';

export default class Main extends Component {
  render () {
    return(
      <div>
        <Switch>
          <Route exact={true} path={process.env.PUBLIC_URL + '/'} component={MovieSearch}/>
          <Route exact={true} path={process.env.PUBLIC_URL + '/movie-search'} component={MovieSearch}/>
          <Route exact={true} path={process.env.PUBLIC_URL + '/movie-detail'} component={MovieDetail}/>
          <Route exact={true} path={process.env.PUBLIC_URL + '/my-favourite-movies'} component={FavouriteMovies}/>
        </Switch>
      </div>
    )
  }
}

